import {useState} from 'react'
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';

import {Paper, Stack, Typography, IconButton, InputBase, Box, Tabs, Tab} from '@mui/material'

import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import SearchIcon from '@mui/icons-material/Search'

import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import FoodPage from './FoodPage'

import EmojiFoodBeverageTwoToneIcon from '@mui/icons-material/EmojiFoodBeverageTwoTone';
import LocalDiningIcon from '@mui/icons-material/LocalDining';

import { grey, amber, } from '@mui/material/colors';

import data from './foods.json';

import { createTheme, ThemeProvider } from '@mui/material/styles';

const categories = [
  {
    text: "全て",
  },
  {
    text: "肉料理",
  },
  {
    text: "魚料理",
  },
  {
    text: "定食",
  },
  {
    text: "イタリアン",
  },
  {
    text: "野菜",
  },
  {
    text: "野菜",
  },
  {
    text: "野菜",
  },
]

const theme = createTheme({
  palette: {
    primary: {
      main: amber[500],
    },
  },
  typography: {
    body1: {
      letterSpacing: 2,
      fontSize: 12,
    },
  }
});


const avrs = {};
const stds = {};

for (var property of ["energy", "protein", "fat", "carb"]) {
  const avr = data.reduce((a, b) => a + b[property], 0) / data.length;
  const std = Math.sqrt(data.reduce((a, b) => (a + ((b[property] - avr) ** 2)), 0) / data.length);
  avrs[property] = avr;
  stds[property] = std;
}

export default function MyList() {
  const [foods, setFoods] = useState(data);
  const [text, setText] = useState("");
  const [categoryId, setCategoryId] = useState(0);

  const [food, setFood] = useState(null);


  const [state, setState] = useState(false);

  const toggleDrawer = (open, food) => (event) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setFood(food)

    setState(open);
  };



  return (
    <ThemeProvider theme={theme}>
    <Stack spacing={1} sx={{pt: 2, boxSizing: 'border-box'}}>
      <Stack direction="row" justifyContent="space-between" spacing={2} alignItems="center" sx={{px: 2, boxSizing: 'border-box'}}>
        <LocalDiningIcon sx={{fontSize: 30}} color="primary"/>
        <MyInput setFoods={setFoods} text={text} setText={setText}/>
      </Stack>

      <MyTabs value={categoryId} setValue={setCategoryId}/>
      <List disablePadding sx={{ width: '100%', px: 2, boxSizing: 'border-box' }}>
        {data.map((food, i) => {
          return(
            <ListItem
              disableGutters
              onClick={toggleDrawer(true, food)}
            >
              <ListItemAvatar sx={{pr: 2}}>
                <Avatar variant="rounded" src={food.img_url} sx={{ width: 80, height: 80, borderRadius: 4 }}/>
              </ListItemAvatar>
              <ListItemText
                primary={
                  <Typography sx={{fontWeight: "bold"}}>
                    {food.name}
                  </Typography>
                }
                secondary={
                  <>
                  <Typography noWrap fontSize={12}>
                    タンパク質より脂質と炭水化物の割合が高い牛丼は、亜鉛・銅・マンガン・ナトリウム・モリブデン・ナイアシン・ビタミンB12等の栄養素を含む高カロリーの食べ物。
                  </Typography>
                  <Typography fontSize={12}>
                  {food.energy} kcal
                  </Typography>
                  <Typography fontSize={12}>
                    <Stack direction="row" spacing={0.5} alignItems="center">
                    <Avatar variant="rounded" sx={{fontSize: 12, width: 14, height: 14}}>P</Avatar>
                    <Typography fontSize={12}>{food.protein}g</Typography>
                    <Avatar variant="rounded" sx={{fontSize: 12, width: 14, height: 14}}>F</Avatar>
                    <Typography fontSize={12}>{food.fat}g</Typography>
                    <Avatar variant="rounded" sx={{fontSize: 12, width: 14, height: 14}}>C</Avatar>
                    <Typography fontSize={12}>{food.carb}g</Typography>
                    </Stack>
                  </Typography>
                  </>
                }
              />
              <IconButton edge="end">
                <ArrowForwardIosIcon />
              </IconButton>
            </ListItem>
          )
        })}
      </List>
    </Stack>
    <SwipeableDrawer
      anchor={"bottom"}
      open={state}
      onClose={toggleDrawer(false)}
      onOpen={toggleDrawer(true)}
    >
      <Box
        onClick={toggleDrawer(false)}
        onKeyDown={toggleDrawer(false)}
      >
        <FoodPage food={food} stds={stds}/>
      </Box>
    </SwipeableDrawer>
    </ThemeProvider>
  );
}


function MyTabs(props) {
  return (
    <Box sx={{ width: "100%", borderBottom: 1, borderColor: 'divider' }}>
      <Tabs
        value={props.value}
        onChange={(event, newValue) => props.setValue(newValue)}
        variant="scrollable"
        scrollButtons={false}
        sx={{
          minHeight: 30,
        }}
      >
        {categories.map((category) => (
          <Tab
            label={
              <Typography><b>{category.text}</b></Typography>
            }
            sx={{p: 0, minHeight: 30, minWidth: 60}}
          />
        ))}
      </Tabs>
    </Box>
  );
}


function MyInput(props) {
  return (
    <Paper
      component="form"
      elevation={0}
      sx={{ borderRadius: 5, display: 'flex', alignItems: 'center', width: '100%', bgcolor: grey[200], height: 40 }}
      onSubmit={(e) => e.preventDefault()}
    >
      <IconButton sx={{ p: '10px' }} aria-label="menu">
        <SearchIcon />
      </IconButton>
      <InputBase
        sx={{ ml: 1, flex: 1 }}
        placeholder="Search Google Maps"
        inputProps={{ 'aria-label': 'search google maps' }}
        value={props.text}
        onChange={(e) => props.setText(e.target.value)}
      />
    </Paper>
  );
}
